// kt hp cc crew c cng department
const sortByDepartment = (crew) => {
  return crew.reduce((acc, cur) => {
    // tm index ca department hin ti co trong bin tch lu(acc) hay cha
    const index = acc.findIndex((a) => a.department === cur.department);
    // nu  tn ti th thm department hin ti(cur) vo department  c(acc[i].dt)
    if (index > -1) {
      acc[index].data.push(cur);
    } else {
      // nu cha tn ti th to thm department mi vo acc(gm tn department v cc gi tr khc vo "data")
      acc.push({ department: cur.department, data: [cur] });
    }
    return acc.sort((a, b) =>
      // sp xp department theo th t ch ci
      a.department > b.department ? 1 : b.department > a.department ? -1 : 0
    );
  }, []);
};

export default sortByDepartment;
